@import "https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap";
h1 {
  text-align: center;
  font-family: "Press Start 2P", cursive;
}

body {
  background: #333;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  display: flex;
  overflow-x: hidden;
}

