  padding: 1em;
}

.rom-selector {
  flex-direction: column;
  align-items: center;
  margin: 1em;
  display: flex;
}

.rom-selector input[type="file"] {
  color: #fff;
  cursor: pointer;
  background: #444;
  border: 2px solid #fff;
  padding: .5em;
  font-family: "Press Start 2P", cursive;
}

.rom-selector select, .rom-selector button {
  color: #fff;
  cursor: pointer;
  background: #444;
  border: 2px solid #fff;
  margin: .5em;
  padding: .5em;
  font-family: "Press Start 2P", cursive;
}

.rom-selector select {
  width: 200px;
  font-size: 16px;
}

