@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

h1 {
    font-family: 'Press Start 2P', cursive;
    text-align: center;
}

body {
    height: 100vh;
    width: 100vw;
    background: #333;
    padding: 0;
    margin: 0;
    overflow-x: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
}

main {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    margin: 0;
}

.console {
    border: 5px solid black;
    padding: 1em;
    background: #2eb1dd;
    color: #fff;
}

.rom-selector {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 1em;
}

.rom-selector input[type="file"] {
    font-family: 'Press Start 2P', cursive;
    padding: 0.5em;
    background: #444;
    color: #fff;
    border: 2px solid #fff;
    cursor: pointer;
}

.rom-selector select,
.rom-selector button {
    font-family: 'Press Start 2P', cursive;
    margin: 0.5em;
    padding: 0.5em;
    background: #444;
    color: #fff;
    border: 2px solid #fff;
    cursor: pointer;
}

.rom-selector select {
    font-size: 16px;
    width: 200px;
}

#load-rom {
    background-color: #2eb1dd;
}

#load-rom:hover, .rom-selector input[type="file"]:hover, .rom-selector select:hover {
    background-color: #1a8ea1;
}

#reload {
    background: #2eb1dd;
    color: #fff;
    font-family: 'Press Start 2P', cursive;
    margin: 1em;
    padding: 0.2em;
    cursor: pointer;
}

#reload:hover {
    background-color: #1a8ea1;
}
